.h1 {
  position: relative;
  font-weight: 900;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.h1,
.h1-wrapper,
.h2 {
  align-self: stretch;
}
.h1-wrapper {
  height: 45px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.h2 {
  margin: 0;
  position: relative;
  font-size: var(--font-size-base);
  font-weight: 400;
  font-family: var(--font-montserrat-alternates);
  color: #aeaeae;
}
.frame-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: var(--gap-12xs);
}
.c {
  position: relative;
  font-size: var(--font-size-sm);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-white);
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.c-wrapper {
  cursor: pointer;
  border: 0;
  padding: var(--padding-mini) 95px;
  background-color: var(--color-black);
  width: 366px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-lg);
  height: 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.gem-stone-1-1-icon {
  width: 120px;
  position: relative;
  height: 120px;
}
.div {
  position: relative;
  font-size: var(--font-size-5xl);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-white);
  text-align: left;
  text-shadow: 0.3px 0 0#000, 0 0.3px 0#000, -0.3px 0 0#000, 0-0.3px 0#000;
}
.button-premium {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: 0 144px;
  background-color: #52d4ff;
  width: 366px;
  border-radius: var(--br-xl);
  box-sizing: border-box;
  height: 189px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.speech-balloon-1 {
  align-self: stretch;
  flex: 1;
  position: relative;
}
.button-support {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-11xl) 121px var(--padding-4xl);
  background-color: #ffa2a2;
  width: 366px;
  border-radius: var(--br-xl);
  box-sizing: border-box;
  height: 189px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main1 {
  align-self: stretch;
  flex-direction: column;
  gap: 27px;
}
.indexhtml,
.main,
.main1 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.main {
  height: 635px;
  flex-direction: column;
  gap: 30px;
}
.indexhtml {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  height: 844px;
  overflow: hidden;
  flex-direction: row;
  padding: 61px var(--padding-xs);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
